<template>
  <div class="container">
    <HeaderTop bookName="Movie List..." authorName="Pradeep" />

    <BookBox :books="books" />

</div>
</template>

<script>

import HeaderTop from './components/HeaderTop.vue'
import BookBox from './components/BookBox.vue'

export default {
  name: 'App',
  components: {    
    HeaderTop,
    BookBox
  },

  data(){
    return {
      books: []
    }

  },

  methods: {

    // promises

      async fetchBooks(){
          const res = await fetch('http://localhost:1234/books')
          const data = await res.json()
          console.log( data )
          return data
      }


  },
  async created(){
    this.books = await this.fetchBooks()
  }
}

</script>

